html {
  scroll-behavior: smooth;
}

body {
  font-family: 'Cascadia Code', monospace;
  margin: 0;
  padding: 0;
}






.navbardiv {
  position: fixed;       /* Always visible on screen */
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  transition: transform 0.3s ease;
}

.navbardiv.hide {
  transform: translateY(-100%);
}

.navbardiv.show {
  transform: translateY(0);
}

.navbar {
  background-color: rgba(255, 255, 255, 0.101); /* Background to make it visible */
  display: flex;
  justify-content: center;
  gap: 6rem;
  padding: 1rem 2rem;
}

.navbar a {
  color: white;
  text-decoration: none;
  font-size: 20px;
  font-weight: bold;
}

.navbar a:hover {
  background-color: #98a0bf46;
}



section {
