/* //////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
                                                                Projects Page
////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////// */








.projects-section {
  margin-right: 100px;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center; /* Center the content horizontally */
  text-align: center;
}

.projects-title {
  font-size: 48px;
  font-weight: bold;
  margin-bottom: 40px;
  padding: 8px 16px;
  border-radius: 8px;
  display: inline-block;
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 30px;
  max-width: 1200px;
  width: 100%;
  justify-content: center;
  align-items: center; /* optional: center cards vertically in each row */
}

.project-card {
  color: white;
  text-decoration: none;
  padding: 20px;
  border-radius: 8px;
  transition: transform 0.6s ease, background-color 0.6s ease;
  display: flex;
  flex-direction: column;
  align-items: center; /* center text and content inside card */
}

.project-card:hover {
  transform: translateY(-10px);
  background-color: #98a0bf46;
}

.project-title {
  font-size: 24px;
  font-weight: 600;
  margin-bottom: 10px;
}

.project-description {
  font-size: 16px;
  color: #cfcfcf;
  margin-bottom: 20px;
}

.project-info {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
  font-size: 14px;
  color: #bbbbbb;
}

.project-info span {
  background: #f3f3f329;
  padding: 4px 10px;
  border-radius: 20px;
}




.MoreProjects a{
  display: inline-block;
  padding: 14px 14px;
  margin-top: 80px;
  background-color: #98a0bf46;
  border-radius: 8px;
  color: white;
  text-decoration: none;
  font-weight: bold;
  transition: transform 0.6s ease, background-color 0.6s ease;

  font-size: 32px;
}


.MoreProjects a:hover{
  transform: translateY(-10px);
  background-color: #3e2bac00;
}





